@import "variables/main.scss";
@import "mixins/main.scss";
@import "grid";
@import "visibility"; // Visibility last for precedence

@font-face {
    font-family: 'Suisse';
    src: url('../fonts/SuisseIntl-Light.ttf');
    font-weight: 300;
}
  
@font-face {
    font-family: 'Suisse';
    src: url('../fonts/SuisseIntl-Regular.otf');
    font-weight: 400;
}
  
@font-face {
    font-family: 'Suisse';
    src: url('../fonts/SuisseIntl-Medium.ttf');
    font-weight: 500;
}
  
@font-face {
    font-family: 'Suisse';
    src: url('../fonts/SuisseIntl-SemiBold.ttf');
    font-weight: 600;
}

*, *:before, *:after{
    box-sizing: border-box;
  }
  
  html, body, div, span, object, iframe, figure, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, code, em, img, small, strike, strong, sub, sup, tt, b, u, i, ol, ul, li, fieldset, form, label, table, caption, tbody, tfoot, thead, tr, th, td, main, canvas, embed, footer, header, nav, section, video{
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    font: inherit;
    vertical-align: baseline;
    text-rendering: optimizeLegibility;
    -webkit-font-smoothing: antialiased;
    text-size-adjust: none;
  }
  
  footer, header, nav, section, main{
    display: block;
  }
  
  body{
    line-height: 1;
  }