@import '../../../assets/scss/main.scss';

$sm: 37.5em;
$md: 48em;
$lg: 64em;
$xl: 75em;

$spacing-sm: 8px;
$spacing-md: 16px;
$spacing-lg: 32px;

$border-sm: 10px;
$border-md: 20px;
$border-lg: 40px;

@mixin breakpoint($point) {
    @if $point == xl {        
        @media (min-width: $xl) {
            @content;
        }
    }
    @if $point == lg {        
        @media (min-width: $lg) {
            @content;
        }
    }
    @if $point == md {
        @media (min-width: $md) {
            @content;
        }
    }
    @if $point == sm {        
        @media (min-width: $sm) {
            @content;
        }
    }
}

.s-404 {
    width: 93px;
    height: 93px;          

    @include breakpoint(md) {
        width: 186px;
        height: 186px;
    }
}