@import '../../../assets/scss/main.scss';

$sm: 37.5em;
$md: 48em;
$lg: 64em;
$xl: 75em;

$spacing-sm: 8px;
$spacing-md: 16px;
$spacing-lg: 32px;

$border-sm: 10px;
$border-md: 20px;
$border-lg: 40px;

@mixin breakpoint($point) {
    @if $point == xl {        
        @media (min-width: $xl) {
            @content;
        }
    }
    @if $point == lg {        
        @media (min-width: $lg) {
            @content;
        }
    }
    @if $point == md {
        @media (min-width: $md) {
            @content;
        }
    }
    @if $point == sm {        
        @media (min-width: $sm) {
            @content;
        }
    }
}

.MainFrame {
    width: 100%; 
    aspect-ratio: 7 / 2;
    background: black;
    background-size: cover;
    overflow: hidden; 
    flex-direction: column; 
    justify-content: flex-start; 
    align-items: flex-start;
    display: flex;

    border-radius: $border-sm;       

    @include breakpoint(md) {
        border-radius: $border-md;         
    }

    @include breakpoint(lg) {
        border-radius: $border-lg;         
    }
}

.Content-404 {
    align-self: stretch; 
    flex-direction: column; 
    justify-content: center; 
    align-items: center;      
    display: inline-flex;
    padding-top: 70px;
    padding-bottom: 112px;

    & > .b-404 {
        display: flex;
        flex-direction: row; 
        justify-content: center; 
        align-items: center; 

        font-family: Suisse;
        font-size: 150px;
        line-height: 170px;
        font-weight: 500;
        margin-bottom: -10px;          

        @include breakpoint(md) {
            font-size: 300px;
            line-height: 347px; 
        }
    }

    & > .Text {
        font-family: Suisse;
        font-size: 24px;
        line-height: 27px;
        font-weight: 400;
        margin-bottom: 34px;
        text-align: center;
        color: #000000B0;

    }

    & > .Button {
        display: block;
        padding: 18px 40px;
        background-color: black;
        border-radius: 9999px;

        font-family: Suisse;
        font-size: 18px;
        line-height: 21px;
        font-weight: 400;
        color: white;
        text-decoration: none;

        &:hover {
            opacity: 0.9;
        }
    }
}